import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'

export function _ToyPreview({ toy, loggedInUser }) {
    return (
        <div className="toy-preview">
            <h3>{toy.name}</h3>
            <h4>{toy.price}$</h4>
            <h5>Category: {toy.type}</h5>
            {toy.inStock && 'This toy is on stock!'}
            {!toy.inStock && 'Out of stock'}
            <div><NavLink to={`/toy/${toy._id}`}>Check It Out!</NavLink></div>
            {loggedInUser && loggedInUser.isAdmin && <div><NavLink to={`/toy/edit/${toy._id}`}>Edit</NavLink></div>}
            <img src={toy.imgUrl} alt=""/>

        </div>
    )
}


const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedInUser: state.userReducer.loggedinUser
    }
}

export const ToyPreview = connect(mapStateToProps)(_ToyPreview)