import React, { Component } from 'react';
import { toyService } from '../services/toyService';
import { connect } from 'react-redux';
import { removeToy, saveToy } from '../store/actions/toyActions'
export class _ToyEdit extends Component {

    state = {
        toy: null
    }
    componentDidMount() {
        toyService.getById(this.props.match.params.id)
            .then(toy => {
                this.setState({ toy })
            })
    }


    uploadImg = async (ev) => {
        const CLOUD_NAME = "dsrtl41yz"
        const UPLOAD_URL = `https://api.cloudinary.com/v1_1/${CLOUD_NAME}/image/upload`

        const formData = new FormData();
        formData.append('file', ev.target.files[0])
        formData.append('upload_preset', 'ml_default');
        try {
            const res = await fetch(UPLOAD_URL, {
                method: 'POST',
                body: formData
            })
            const data = await res.json()
            // console.log(data);
            this.setState({ toy: { ...this.state.toy, imgUrl: data.secure_url } }, () => console.log(this.state))
            return data

        } catch (err) {
            console.log('Got Error uploading the img:', err);
        }

    }




    handleInput = ({ target }) => {
        const field = target.name
        const value = (target.type === 'number') ? +target.value : target.value
        this.setState(prevState => {
            return {
                toy: {
                    ...prevState.toy,
                    [field]: value
                }
            }
        }, () => console.log(this.state, '@@@***'))
    }

    onSaveToy = (ev) => {
        ev.preventDefault()
        this.props.saveToy(this.state.toy)
        setTimeout(() => this.props.history.push('/toy'), 400)


    }

    render() {
        const { toy } = this.state
        if (!toy) return <div>Loading...</div>
        return (
            <div className="edit-container">
                <h3>Want to edit your toy info? This is the right place!</h3>
                <form onSubmit={this.onSaveToy}>
                    <input onChange={this.handleInput} type="text" name="name" defaultValue={toy.name} />
                    <input onChange={this.handleInput} type="text" name="type" defaultValue={toy.type} />
                    <input onChange={this.handleInput} type="number" name="price" defaultValue={toy.price} />
                    {toy.imgUrl && <img src={toy.imgUrl} alt='No Img' />}
                    {/* <label> */}
                <input type="file" onChange={this.uploadImg} />
                    {/* </label> */}
                    <button>SAVE</button>
                </form>
            </div>
        );
    }
}





const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys
    }
}
const mapDispatchToProps = {
    removeToy,
    saveToy
}
export const ToyEdit = connect(mapStateToProps, mapDispatchToProps)(_ToyEdit)