import React, { Component } from 'react'
import { TextField, Button } from '@material-ui/core';
import { connect } from 'react-redux';
import { onLogin } from '../store/actions/userActions.js'

export class _Login extends Component {
    state = {
        username: '',
        password: ''
    }
    handleChange = ({ target }) => {
        const { name, value } = target
        this.setState({ [name]: value })

    }
    onLogin = async ev => {
        if (!this.state.username || !this.state.password) return alert('Please enter user/password')
        this.props.onLogin(this.state)
    }

    render() {
        const { loggedInUser } = this.props
        console.log('logged in:',loggedInUser);
        return (
            <div>
                <h1>Login page</h1>
                {!loggedInUser && <form className="login-form" onSubmit={this.onLogin}>
                    <TextField autoFocus required onChange={this.handleChange} name="username" id="outlined-basic" label="Username" variant="outlined" />
                    <TextField required onChange={this.handleChange} name="password" id="outlined-basic" label="Password" variant="outlined" />
                    <Button variant="contained" color="primary" onClick={this.onLogin}>Submit</Button>
                </form>}
                {loggedInUser && <h2>Welcome,{loggedInUser.username}</h2>}

            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedInUser: state.userReducer.loggedinUser
    }
}
const mapDispatchToProps = {
    onLogin
}
export const Login = connect(mapStateToProps, mapDispatchToProps)(_Login)
