import React, { Component } from 'react'
import { connect } from 'react-redux';
import { AddReview } from '../cmps/AddReview.jsx';
import { ChatRoom } from '../cmps/ChatRoom.jsx';
import { toyService } from '../services/toyService';
import { removeToy } from '../store/actions/toyActions'
export class _ToyDetails extends Component {

    state = {
        toy: null
    }

    async componentDidMount() {
        const toy = await toyService.getById(this.props.match.params.id)
        this.setState({ toy })
    }

    onDeleteToy = () => {
        this.props.removeToy(this.state.toy)
        setTimeout(() => this.props.history.push('/toy'), 500)
    }

    render() {
        const { toy } = this.state
        const { loggedInUser } = this.props
        if (!toy) return <span>Loading...</span>

        return (
            <div className="details-container">
                <h3>{toy.name}</h3>
                <h4>{toy.price}$</h4>
                <h5>Category: {toy.type}</h5>
                {toy.inStock ? 'This toy is on stock!' : 'Out of stock'}
                <h5>Created At {toy.createdAt}</h5>
                {toy.imgUrl && <img src={toy.imgUrl} alt='No Img' />}
                {loggedInUser && loggedInUser.isAdmin && <button onClick={this.onDeleteToy}>Delete Toy</button>}
                <ChatRoom toy={toy} />
                <AddReview toyId={toy._id} />
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedInUser: state.userReducer.loggedinUser

    }
}
const mapDispatchToProps = {
    removeToy
}


export const ToyDetails = connect(mapStateToProps, mapDispatchToProps)(_ToyDetails)
