
import React, { Component } from 'react';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import TextField from '@material-ui/core/TextField';
import { Formik, Form } from 'formik';

export class ToyAdd extends Component {

    handleChange = (ev) => {
        const toyName = ev.target.value
        this.setState({ toyName })
    }

    submitForm = (ev) => {
        ev.preventDefault()
        console.log('adding:',this.state);
        this.props.onAddToy(this.state.toyName)
    }



    render() {
        return (
            <section>
                {/* <form onSubmit={this.submitForm}>
                    <TextField onChange={this.handleChange} type="text" placeholder="Add Toy..." id="outlined-basic" label="New Toy" variant="outlined" />
                    <Fab color="primary" aria-label="add">
                        <AddIcon onClick={this.submitForm} />
                    </Fab>
                </form> */}
                {/* {Basic()} */}

                <Formik
                    initialValues={{ text: '' }}

                    onSubmit={(values, { setSubmitting }) => {
                        setTimeout(() => {
                            alert(JSON.stringify(values, null, 2));
                            setSubmitting(false);
                        }, 400);
                    }}
                >
                    {({ isSubmitting }) => (
                        <Form>
                            <TextField name="text" onChange={this.handleChange} type="text" placeholder="Add Toy..." id="outlined-basic" label="New Toy" variant="outlined" />

                            <Fab color="primary" aria-label="add">
                                <AddIcon onClick={this.submitForm} type="submit" disabled={isSubmitting} />
                            </Fab>

                        </Form>
                    )}
                </Formik>
            </section>

        );
    }
}


