const initialState = {
    toys: []
}
export function toyReducer(state = initialState, action) {
    switch (action.type) {
        case 'SET_TOYS':
            return {
                ...state,
                toys: action.toys
            }
        case 'REMOVE_TOY':
            return {
                ...state,
                toys: state.toys.filter(toy => {
                    return toy._id !== action.toy._id
                })
            }
        case 'ADD_TOY':
            return { ...state, toys: [action.newToy, ...state.toys] }

        case 'UPDATE_TOY':
        case 'UPDATE_TODO':
            // return {
            //     ...state, todos: state.todos.map(todo => {
            //         if (todo._id === action.todo._id) return action.todo
            //         return todo;
            //     })
            // }
            return {
                ...state, toys: state.toys.map(toy => {
                    if (toy._id === action.toyToSave._id) return action.toyToSave
                    return toy;
                })
            }
        default:
            return state;
    }
}