import React, { Component } from 'react';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';
import { ButtonGroup, Button } from '@material-ui/core';

export class _About extends Component {

    state = {
        lat: 32.0853,
        lng: 34.7818
    }


    onMarkerClick = (ev) => {
        console.log(ev);
    }


    onInfoWindowClose = (ev) => {
        console.log(ev);
    }


    onMapClicked = (mapProps, map, ev) => {
        this.setState({ lat: ev.latLng.lat(), lng: ev.latLng.lng() })
    }
    changePos = (city) => {
        const pos = {
            lat: '',
            lng: ''
        }
        switch (city) {
            case 'haifa':
                pos.lat = 32.794044
                pos.lng = 34.989571
                break;
            case 'bat-yam':
                pos.lat = 32.017136
                pos.lng = 34.745441
                break;
            case 'eilat':
                pos.lat = 29.55805  
                pos.lng = 34.94821
                break;

            default:
                pos.lat = 32.017136
                pos.lng = 34.745441
                break;
        }
        this.setState(pos)

    }

    render() {
        return (
            <section>
                <h1>About Page</h1>
                <ButtonGroup color="primary" variant="contained" aria-label="contained primary button group">
                    <Button onClick={() => this.changePos('haifa')} >Haifa</Button>
                    <Button onClick={() => this.changePos('batyam')} >Bat-Yam</Button>
                    <Button onClick={() => this.changePos('eilat')} >Eilat</Button>
                </ButtonGroup>
                <Map initialCenter={this.state} center={this.state} google={this.props.google} zoom={10} onClick={this.onMapClicked}>

                    <Marker onClick={this.onMarkerClick}
                        name={'Current location'} />
                    <Marker position={{ lat: 32.2953, lng: 34.8822 }} />
                    <Marker position={{ lat: 32.3803, lng: 35.0822 }} />

                    <InfoWindow onClose={this.onInfoWindowClose}>
                        <div>
                            <h1>InfoWindow</h1>
                        </div>
                    </InfoWindow>
                </Map>
            </section>
        );
    }
}

export const About = GoogleApiWrapper({
    apiKey: ('AIzaSyDV3aMtXNZ4Q87UKlasdj0gHhEP2xEEqFY')
})(_About)
