import Axios from 'axios'


const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? '/api/toy'
    : 'http://localhost:3030/api/toy';

// const BASE_URL = 'http://localhost:3035/api/toy'
const axios = Axios.create({
    withCredentials: true
});
const resolveData = res => res.data

export const toyService = {
    query,
    getById,
    remove,
    save
}

function query(filter) {
    // return axios.get(BASE_URL,{params:filterBy}).then(resolveData)
    // .then(toys=>toys)
    let query = '';
    if (filter) {
        query = '?' + Object.keys(filter).map(key => key + '=' + filter[key]).join('&');
    }
    console.log('query is',query);
    return axios.get(`${BASE_URL}${query}`).then(resolveData)


    // var queryParams = new URLSearchParams()
    // // posts?_sort=views&_order=asc
    // if (filterBy.name) return axios.get(`${BASE_URL}?_sort=name&_order=asc`).then(resolveData)
    // // if(filterBy==='names') queryParams.set('_sort=name&_order=asc')
    // if (filterBy === 'price') return axios.get(`${BASE_URL}?_sort=price&_order=asc`).then(resolveData)
    // if (filterBy.name) queryParams.set('name', filterBy.name)
    // if (filterBy.inStock) queryParams.set('inStock', filterBy.inStock)
    // if (filterBy.inStock) queryParams.set('inStock', filterBy.inStock)
    // if (filterBy.inStock === 'all') return axios.get(`${BASE_URL}`).then(resolveData)

    // return axios.get(`${BASE_URL}?${queryParams}`)
    //     .then(resolveData)
}


function getById(toyId) {
    return axios.get(`${BASE_URL}/${toyId}`)
        .then(resolveData)

}

function remove(toyId) {
    return axios.delete(`${BASE_URL}/${toyId}`)
}

function save(toy) {

    // if (toy._id) {
    //     return axios.put(`${BASE_URL}/${toy._id}`, toy).then(resolveData)
    //         .then(savedToy => savedToy)
    // } else {
    //     return axios.post(BASE_URL, toy).then(resolveData)
    //         .then(savedToy => savedToy)

    if (toy._id) {
        return axios.put(`${BASE_URL}/${toy._id}`, toy)
    } else {
        const toyToPost = {
            price: 56,
            name: toy,
            type: "Funny",
            inStock: true

        }
        return axios.post(BASE_URL, toyToPost).then(resolveData)
    }
}


