import React, { Component } from 'react';
import { connect } from 'react-redux';
import { loadToys } from '../store/actions/toyActions';
import { Bar, Pie } from 'react-chartjs-2'

export class _Dashboard extends Component {
    componentDidMount() {
        this.props.loadToys()
    }

    getObjectMap = () => {
        const objectMap = this.props.toys.reduce((acc, toy) => {
            if (!acc[toy.type]) acc[toy.type] = 0
            acc[toy.type] += toy.price
            return acc
        }, {})
        return objectMap;
    }

    getCounterMap = () => {
        const counterMap = this.props.toys.reduce((acc, toy) => {
            if (!acc[toy.type]) acc[toy.type] = 0
            acc[toy.type]++
            return acc

        }, {})
        return counterMap
    }

    getBarData = () => {

        const typeNames = Object.keys(this.getObjectMap())
        const toyPrices = Object.values(this.getObjectMap())
        const amontOfType = Object.values(this.getCounterMap())
        const avgPrices = toyPrices.map((price, idx) => {
            return +(price / amontOfType[idx]).toFixed(0)

        })

        return {
            labels: typeNames,
            datasets: [
                {
                    label: 'Average price',
                    backgroundColor: 'rgba(255,99,132,0.2)',
                    borderColor: 'rgba(255,99,132,1)',
                    borderWidth: 2,
                    hoverBackgroundColor: 'rgba(255,99,132,0.4)',
                    hoverBorderColor: '#ff6384',
                    data: avgPrices
                }
            ]
        }

    }

    getPieData = () => {
        const objectMap = this.props.toys.reduce((acc, toy) => {
            const year = toy.createdAt.substring(0, 4)
            if (!acc[year]) acc[+year] = 0
            acc[year]++
            return acc
}, {})

        return {
            labels: Object.keys(objectMap),
            datasets: [{
                data: Object.values(objectMap),
                backgroundColor: [
                    '#FF6384',
                    '#36A2EB',
                    '#FFCE56',
                    '#FFEE11',
                    '#40434E',
                    '#52154E',
                    '#C17817',
                    '#3F784C',
                    '#B20D30',
                    '#F0E2E7',
                    '#3F84E5',
                    '#af73e1'
                ],
                hoverBackgroundColor: [
                    '#FF6384',
                    '#36A2EB',
                    '#FFCE56',
                    '#FFEE11',
                    '#40434E',
                    '#52154E',
                    '#C17817',
                    '#3F784C',
                    '#B20D30',
                    '#F0E2E7',
                    '#3F84E5',
                    '#af73e1'
                ]
            }]
        };
    }
    render() {
        if (!this.props.toys.length) return <div>Loading</div>
        return (
            <section className="dashboard-container">
                <h1>Dashboard</h1>
                <h2>Average price per Toy category/type</h2>
                <Bar data={this.getBarData()} width={5} height={1} options={{
                    maintainAspectRatio: true, scales: { yAxes: [{ ticks: { beginAtZero: true, min: 0,  } }] }
                }} />
                <h2>Toys per year</h2>
                <Pie width={3} height={1} data={this.getPieData()} />

            </section>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys
    }
}
const mapDispatchToProps = {
    loadToys,

}


export const Dashboard = connect(mapStateToProps, mapDispatchToProps)(_Dashboard)