import React, { Component } from 'react';
import { connect } from 'react-redux';
import { loadToys, addToy } from '../store/actions/toyActions';
import { ToyList } from '../cmps/ToyList';
import { ToyFilter } from '../cmps/ToyFilter';
import { ToyAdd } from '../cmps/ToyAdd';

export class _ToyApp extends Component {
    state = {
        filterBy: null
    }


    componentDidMount() {
        this.props.loadToys()
    }
    onSetFilter = (filterBy) => {
        // this.props.loadToys(filterBy)
        this.setState({ filterBy }, () => this.props.loadToys(this.state.filterBy))
    }
    onAddToy = (toyName) => {
        this.props.addToy(toyName)
    }


    render() {
        const { toys, loggedInUser } = this.props
        if (!toys) return <div>Loading...</div>
        return (
            <div className="toy-app">
                <h1>Toy App</h1>
                {loggedInUser && loggedInUser.isAdmin && <ToyAdd onAddToy={this.onAddToy} />}
                <ToyFilter onSetFilter={this.onSetFilter} onSortName={this.onSortName} onSortPrice={this.onSortPrice} />
                <ToyList toys={toys} />
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedInUser: state.userReducer.loggedinUser
    }
}
const mapDispatchToProps = {
    loadToys,
    addToy
}
export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp)
