import React, { Component } from 'react';
import { connect } from 'react-redux';
import { socketService } from '../services/socketService';

export class _ChatRoom extends Component {

    state = {
        msg: { from: 'Guest', txt: '' },
        msgs: [],
        topic: this.props.toy._id,
        userTyping: ''
    };

    componentDidMount() {
        // const user = this.getUser()
        const { loggedInUser } = this.props
        if (loggedInUser) this.setState({ msg: { ...this.state.msg, from: loggedInUser.username } })
        // this.setState({ msg: { ...this.state.msg, from: user } })
        socketService.setup();
        socketService.emit('chat topic', this.state.topic);
        socketService.on('chat addMsg', this.socketAddMsg)
        socketService.on('getMsgs', this.getHistory)
        socketService.on('user typing', this.setUserTyping)
    }

    setUserTyping = (user) => {
        this.setState({ userTyping: user })

    }
    componentWillUnmount() {
        socketService.off('chat addMsg')
        socketService.terminate()
    }
    getHistory = (history) => {
        this.setState({ msgs: history })
    }
    socketAddMsg = (msg) => {
        this.setState(prevState => {
            return {
                msgs: [...prevState.msgs, msg]
            }
        }, console.log(this.state.msgs))


    }


    msgHandleChange = (ev) => {
        socketService.emit('is typing', this.state.msg.from)
        const { name, value } = ev.target;
        if(!value) socketService.emit('is typing','')
        this.setState(prevState => {
            return {
                msg: {
                    ...prevState.msg,
                    [name]: value
                }
            };
        });
    }
    sendMsg = (ev) => {
        ev.preventDefault();
        socketService.emit('chat newMsg', this.state.msg);
        this.setState({ msg: { ...this.state.msg, txt: '' }, isTyping: false });
    }

    render() {
        const { userTyping } = this.state
        return (
            <div className="chat-box">
                <h3>Lets Talk About {this.props.toy.name}</h3>
                {userTyping && < div > {userTyping} is typing...</div>}
               <div className="main-chat">
                {
                    this.state.msgs.length ?
                        <ul className="chat-container">
                            {this.state.msgs.map((msg, idx) => {
                                return <li key={idx}>
                                    {msg.from}: {msg.txt}
                                </li>
                            })}
                        </ul> : ''
                }
                 <form onSubmit={this.sendMsg}>
                    <input autoComplete="off" autoFocus
                        type="text"
                        value={this.state.msg.txt} className="chat-input"
                        onChange={this.msgHandleChange}
                        name="txt"
                    />
                    <button>Send</button>
                </form>
            </div ></div>
        );
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedInUser: state.userReducer.loggedinUser

    }
}
export const ChatRoom = connect(mapStateToProps)(_ChatRoom)
