import { toyService } from "../../services/toyService"

export function loadToys(filter) {
    return dispatch => {
        toyService.query(filter).then(toys => {
            dispatch({ type: 'SET_TOYS', toys })
        })
    }
}

export function removeToy(toy) {
    return dispatch => {
        toyService.remove(toy._id).then(() => {
            dispatch({ type: 'REMOVE_TOY', toy })
        })
    }
}

export function addToy(toyName) {
    console.log('im here:',toyName);
    return dispatch => {
        toyService.save(toyName).then((newToy => {
            dispatch({ type: 'ADD_TOY', newToy })
        }))
    }
}

export function saveToy(toyToSave) {
    return dispatch => {
        toyService.save(toyToSave).then(() => {
            dispatch({ type: 'UPDATE_TOY', toyToSave })
        })
    }
}