
import React, { Component } from 'react';

import { ButtonGroup, Button } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

export class ToyFilter extends Component {
    state = {
        filter: {
            filterText: '',
            inStock: 'all',
            sortBy: 'price'

        },

    }

    componentDidMount() {
    }

    handleChange = ({ target }) => {
        console.log(target.value, '@TARGET');
        const field = target.name
        const value = target.value
        this.setState(prevState => ({ filter: { ...prevState.filter, [field]: value } }), () => {
            this.props.onSetFilter(this.state.filter); console.log(this.state)
        })
    }

    submitForm = (ev) => {
        ev.preventDefault()
        this.props.onSetFilter(this.state.filter)
    }
    // onSortName = () => {
    //     this.props.onSortName()
    // }
    // onSortPrice = () => {
    //     this.props.onSortPrice()
    // }
    setSort = (sortBy) => {
        this.setState({ filter: { ...this.state.filter, sortBy } },
            () => this.props.onSetFilter(this.state.filter))

    }
    render() {
        return (
            <div className="toy-filter">


                <form onSubmit={this.submitForm}>
                    <TextField className="search-input" onChange={this.handleChange} type="text" name="filterText" placeholder="Search toy..." id="outlined-basic" label="SEARCH" variant="outlined" />

                    <Select name="inStock"
                        value={this.state.filter.inStock}
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        onChange={this.handleChange}>
                        <MenuItem value='all'>All</MenuItem>
                        <MenuItem value={'yes'}>In stock</MenuItem>
                        <MenuItem value={'no'}>Out of stock</MenuItem>
                    </Select>


                </form>
                <ButtonGroup color="primary" variant="contained" aria-label="contained primary button group">
                    <Button onClick={() => this.setSort('price')} >Sort By Price</Button>
                    <Button className="special-btn" onClick={() => this.setSort('name')} >Sort By Name</Button>
                </ButtonGroup>

            </div >
        );
    }
}

